
<template>
  <Navbar/>
  <Header/>
  <div class="container">
    <div class="row">
      <div class="col-lg-3 col-sm-12">
        <div className="d-none d-md-block">
          <SearchKeywords />
          <FilterSearch :content="filters" />
          </div>
        <FiltersButton />
      </div>  
      <div class="col-lg-9 col-sm-12">
        <JobBlock :content="cards" />;
      </div>
    </div>
  </div>
  <AddGoogleMap/>
  <div id="banner-ad" class="ad" >
      
  </div>
  <Footer/>
</template>

<script>
import CardData from "./assets/constants/CardData.json";
import FilterData from "./assets/constants/FilterSearchData.json";
import Navbar from "./components/Navbar.vue";
import Header from "./components/Header.vue";
import FilterSearch from "./components/FilterSearch.vue";
import FiltersButton from "./components/FilterButton.vue";
import JobBlock from "./components/JobBlock.vue";
import SearchKeywords from "./components/SearchKeyword.vue";
import AddGoogleMap from "./components/AddGoogleMap";
import Footer from "./components/Footer.vue";


export default {
  name: "App",
  data() {
    return {
      cards:CardData.data,
      filters:FilterData.data,
    };
  },
  mounted() {
    let  googletag = window.googletag || {cmd: []};
      googletag.cmd.push(function() {
        googletag
            .defineSlot(
                '/6355419/Travel/Europe/France/Paris', [300, 250], 'banner-ad')
            .addService(googletag.pubads());
        googletag.enableServices();
      });

    googletag.cmd.push(function() {
          googletag.display('banner-ad');
        });
  },
  components: {
    Navbar,
    Header,
    FilterSearch,
    FiltersButton,
    JobBlock,
    SearchKeywords,
    AddGoogleMap,
    Footer,
  },
  
};
</script>

<style>
  .ad{
width: 300px;
 height: 250px;
  }
</style>

