<template>
  <div class="login d-flex">
    <div id="google-signin-button" v-if="!isSignedIn"></div>
    <a class="nav-link active" v-if="isSignedIn">{{userName}}</a>
    <button v-if="isSignedIn" v-on:click="signOut" class="btn btn-outline-secondary">Sign out</button>
  </div>
</template>
<script>
export default {
  
  name: "Login",
  data() {
    return {
      isSignedIn: false,
      userName: "",
    };
  },
  props: {},
  mounted() {
    let gapi = window.gapi;
    gapi.signin2.render("google-signin-button", {
      onsuccess: this.onSignIn,
    });
  },
  methods: {
    onSignIn(user) {
      this.isSignedIn = true;
      const profile = user.getBasicProfile();
      console.log(profile);
      console.log(profile.qf); //account name
      this.userName = profile.qf
    },
    signOut() {
      this.isSignedIn = false;
      let gapi = window.gapi;
      var auth2 = gapi.auth2.getAuthInstance();
      auth2.signOut();
      location.reload();
    },
  },

};
</script>


